<template>
  <div id="container"></div>
</template>

<script>
// 引入地图包
import AMapLoader from '@amap/amap-jsapi-loader';

export default {
  name: "mapContainer",
  data() {
    return {
      //此处不声明 map 对象，可以直接使用 this.map赋值或者采用非响应式的普通对象来存储。
      //map:null,
    }
  },
  methods: {
    // 初始化地图
    initMap() {
      AMapLoader.load({
        key: "613937de0614a156381934cab2a8f435",             // 申请好的Web端开发者Key，首次调用 load 时必填
        version: "2.0",      // 指定要加载的 JSAPI 的版本，缺省时默认为 1.4.15
        plugins: [],       // 需要使用的的插件列表，如比例尺'AMap.Scale'等
      }).then((AMap) => {
        this.map = new AMap.Map("container", {  //设置地图容器id
          viewMode: "3D",    //是否为3D地图模式
          terrain: true,   // 开启地形图
          zoom: 17,           //初始化地图级别
          center: [113.215344, 23.280506], //初始化地图中心点位置
          pitch: 0,
          rotation: 0,
          // 隐藏默认楼块
          features: ['bg', 'road', 'point'],
          mapStyle: 'amap://styles/light',
          layers: [
            // 卫星
            new AMap.TileLayer.Satellite(),
            // 路网
            new AMap.TileLayer.RoadNet(),
            // 楼块图层
            new AMap.Buildings({
              zooms: [16, 18],
              zIndex: 10,
              heightFactor: 2 //2倍于默认高度，3D下有效
            })
          ],
        });
      }).catch(e => {
        console.log(e);
      })
    },
  },
  mounted() {
    //DOM初始化完成进行地图初始化
    this.initMap();
  }
}
</script>

<style scoped>
#container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  object-fit: cover;
  padding: 0px;
  margin: 0px;
  width: 100%;
  height: 100vh;
}
</style>