<template>
  <div>
    <el-dropdown class="dropDownBox" @command="handleCommand" v-if="menuDisplay">
      <el-button type="primary" class="menuBtn">
        <img src="@/assets/svgImg/menu.png" width="30px" height="30px"/>
      </el-button>
      <el-dropdown-menu slot="dropdown" class="menuItem">
        <el-dropdown-item command="/">首页</el-dropdown-item>
        <el-dropdown-item command="/map">学校地图</el-dropdown-item>
        <el-dropdown-item command="/vrmap">云全景赏校</el-dropdown-item>
        <el-dropdown-item disabled>3D全景赏校</el-dropdown-item>
        <el-dropdown-item disabled>学生评价管理系统</el-dropdown-item>
        <el-dropdown-item command="/about">关于我们</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </div>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
    
    }
  },
  methods: {
    handleCommand(command) {
      this.$router.push(command)
    }
  },
  computed: {
    menuDisplay() {
      const screenWidth = document.body.clientWidth
      if(this.$route.path !== '/about') return true
      return screenWidth > 650
    }
  }
}
</script>

<style scoped>
.el-menu.el-menu--horizontal {
  border-bottom: solid 0px #e6e6e6;
}
.dropDownBox {
  position: relative;
  top: 10px;
  right: 20px;
}
.menuBtn {
  position: relative;
  bottom: 5px;
  right: 18px;
  width: 30px;
  height: 30px;
  background-color: transparent;
  border: 0;
}
.menuItem li:hover {
  color: white;
  background-image: url("@/assets/menubackground.jpg");
}

@media screen and (max-width: 1010px){
  .dropDownBox {
    top: 5px;
    right: 10px;
  }
}
@media screen and (max-width: 768px){
  .dropDownBox {
    top: 1px;
